import React, { Component } from 'react';
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import { withTranslation } from 'react-i18next';


import 'react-vertical-timeline-component/style.min.css';

class Resume extends Component {
    render() {
        const { t } = this.props;
        const firstTechSummary = t('resume-first-tech-summary', { returnObjects: true });
        const secondTechSummary = t('resume-second-tech-summary', { returnObjects: true });

        return(
            <VerticalTimeline>
            <VerticalTimelineElement
                className="vertical-timeline-element--work"
                contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                date="2018 - Present"
                iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
            >
                <h2 className="vertical-timeline-element-title"> {t("resume-first-title")}  </h2>

                <h4 className="vertical-timeline-element-subtitle"> {t("resume-first-subtitle")}  </h4>
                <p>
                {t("resume-first-text")}
                </p>
                <p>
                {firstTechSummary.map(item => {
                    return <span className="badge badge-dark px-1 mx-1">{ item }</span>
                })}
                </p>
            </VerticalTimelineElement>
            <VerticalTimelineElement
                className="vertical-timeline-element--work"
                contentStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
                date="2016 - 2018"
                iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
            >
                <h2 className="vertical-timeline-element-title">{t("resume-second-title")} </h2>
                <h4 className="vertical-timeline-element-subtitle"> {t("resume-second-subtitle")}</h4>
                <p>
                {t("resume-second-text")}
                </p>
                <p>
                {secondTechSummary.map(item => {
                    return <span className="badge badge-dark px-1 mx-1">{ item }</span>
                })}
                </p>
            </VerticalTimelineElement>
            <VerticalTimelineElement
                className="vertical-timeline-element--work"
                contentStyle={{ background: 'rgb(0,0,0)', color: '#fff' }}
                date="2016 - 2018"
                iconStyle={{ background: 'rgb(0,0,0)', color: '#fff' }}
            >
                <h2 className="vertical-timeline-element-title">{t("resume-third-title")} </h2>
                <h4 className="vertical-timeline-element-subtitle"> {t("resume-third-subtitle")}</h4>
                <p>
                {t("resume-third-text")}
                </p>
            </VerticalTimelineElement>
            </VerticalTimeline>
        )
    }
}

export default withTranslation()(Resume)